@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

button {
  outline: none;
  &:active, &:focus {
    outline: none; } }

input {
  outline: none;
  &:active, &:focus {
    outline: none; } }

.row {
  margin: 0; }

main {
  font-family: 'Roboto', sans-serif; }

.page-wrapper {
  background-color: #ececec;
  width: 100%;
  height: 100%;
  padding-bottom: 90px;
  overflow-x: hidden;
  .page {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto; }
  @media screen and (max-width: 600px) {
    padding-bottom: 30px; } }

.home-page-sidebar {
  width: 330px;
  min-width: 330px;
  padding: 0 15px;
  box-sizing: content-box;
  @media screen and (max-width: 950px) {
    max-width: 100%;
    width: 100%; } }

.home-page-content {
  width: calc(100% - 370px);
  padding: 0 5px 0 15px;
  box-sizing: border-box;
  @media screen and (max-width: 950px) {
    width: 100%;
    padding: 0 15px;
    margin-top: -6px; } }

.items-row {
  padding: 14px 20px;
  margin-top: 40px;
  background-color: #fff;
  border-radius: 8px;
  h3 {
    font-size: 24px;
    line-height: 2;
    color: $dark-color;
    margin-left: 5px; }
  .items-block {
    display: flex;
    justify-content: space-between;
    margin-top: 20px; }
  @media screen and (max-width: 1100px) {
    .items-block {
      justify-content: flex-start; } }
  @media screen and (max-width: 600px) {
    margin-top: 15px;
    padding: 14px 5px; } }

@media screen and (max-width: 600px) {
  .small-delete {
    display: none; } }
