.carousel-wrapper {
  margin-top: 10px; }

.carousel {
  width: 100%;
  max-height: 500px;
  height: 100%;
  overflow: hidden;
  border-radius: 5px; }

.carousel {
  .slide {
    padding: 0 !important;
    background-color: #fff; }
  .control-dots .dot {
    &.selected, &:hover {
      padding: 0 !important; }
    &:focus {
      outline: none !important; } } }

.inner-slide {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    max-height: 100%; } }

