@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

h2 {
  font-size: 24px;
  font-weight: 500;
  color: $dark-color;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
  margin-left: 5px;
  margin-bottom: 20px; }

.cart-modal-window {
  padding: 10px;
  color: $dark-color;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
  min-width: 400px;
  min-height: 100px;
  .blank-total {
    text-align: center;
    font-size: 20px;
    margin-top: 30px;
    color: $main-color; }
  .total {
    position: relative;
    margin-top: 30px;
    margin-right: 5px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 16px;
    .cart-btn {
      width: 230px;
      height: 40px; }
    span {
      margin-right: 15px;
      font-weight: 500; } }
  @media screen and (max-width: 950px) {
    min-width: 100%;
    width: 100%; }
  @media screen and (max-width: 650px) {
    h2 {
      width: 100%;
      margin-bottom: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #ddd; } }
  @media screen and (max-width: 450px) {
    .total {
      flex-wrap: wrap;
      justify-content: flex-start;
      margin-left: 10px;
      span {
        width: 100%; }
      .cart-btn {
        margin-top: 10px;
        width: 100%; } } } }

.cart-item {
  list-style-type: none;
  display: flex;
  padding: 12px 20px;
  align-items: center;
  border-radius: 5px;
  border: 1px solid #fff;
  transition: all .3s ease;
  &:hover {
    border: 1px solid $main-color; }
  @media screen and (max-width: 500px) {
    padding: 12px 5px; } }

.preview-img {
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%; }
  @media screen and (max-width: 500px) {
    min-width: 70px;
    min-height: 70px; } }

.preview-body {
  position: relative;
  padding-left: 15px;
  display: flex;
  .preview-text {
    width: 285px;
    margin-right: 15px;
    a {
      font-size: 15px;
      cursor: default; } }
  .preview-counter {
    min-width: 45px;
    margin-right: 15px;
    display: flex;
    align-items: center; }
  .preview-price {
    min-width: 95px;
    margin-right: 15px;
    display: flex;
    align-items: center;
    cursor: default; }
  @media screen and (max-width: 950px) {
    flex-wrap: wrap;
    max-width: 400px;
    .preview-text {
      width: 400px;
      height: 20px;
      overflow: hidden;
      margin-bottom: 15px;
      white-space: nowrap;
      text-overflow: ellipsis; } }
  @media screen and (max-width: 650px) {
    max-width: 350px;
    .preview-text {
      width: 300px; } }
  @media screen and (max-width: 500px) {
    max-width: 100%;
    .preview-text {
      width: 270px;
      height: 45px;
      white-space: normal;
      margin-bottom: 10px; }
    .preview-counter {
      width: 20%; }
    .preview-actions {
      width: 100%;
      margin-top: 10px; } }
  @media screen and (max-width: 380px) {
    .preview-text {
      max-width: 240px;
      width: 100%; } } }


.preview-actions {
  display: flex;
  .preview-action {
    display: block;
    width: 22px;
    height: 22px;
    margin: auto 3px; }
  .ondecrease {
    background: url('../../assets/minus.svg') no-repeat;
    &:hover {
      background: url('../../assets/minus-hover.svg') no-repeat; } }
  .onincrease {
    background: url('../../assets/plus.svg') no-repeat;
    &:hover {
      background: url('../../assets/plus-hover.svg') no-repeat; } }
  .ondelete {
    background: url('../../assets/error.svg') no-repeat;
    &:hover {
      background: url('../../assets/error-hover.svg') no-repeat; } }
  @media screen and (max-width: 950px) {
    margin-left: auto; }
  @media screen and (max-width: 500px) {
    margin-left: 0; } }


.ReactModal__Overlay--after-open {
  background-color: rgba(0, 0, 0, 0.54) !important; }
