.loader-wrapper {
  background-color: #fff;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.loader-container {
  font-size: 10px;
  position: relative;
  width: 100px;
  height: 100px;
  display: inline-block;
  text-align: center;
  border-collapse: collapse;
  vertical-align: top; }

.loader-1 {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  margin: -25px auto 0;
  border-radius: 50%;
  background: #ffffff;
  background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 50%);
  background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 50%);
  background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 50%);
  background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 50%);
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 50%);
  position: relative;
  -webkit-animation: loader-1 1.5s infinite linear;
  animation: loader-1 1.5s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  &:before {
    content: '';
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    background: #33cc33;
    border-radius: 100% 0 0 0; }
  &:after {
    content: '';
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #fff;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; } }

@-webkit-keyframes loader-1 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }


@keyframes loader-1 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }
