@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

main {
  font-family: 'Roboto', sans-serif; }

.column-12 {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  @media screen and (max-width: 600px) {
    padding: 0; } }

.item-navigation {
  height: 65px;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 28px;
  .item-code {
    margin-left: auto;
    font-size: 15px; }
  .capitalize {
    text-transform: capitalize; }
  .breadcrumbs {
    font-size: 13px;
    ul {
      padding: 0;
      margin-bottom: 0;
      li {
        display: inline-block;
        line-height: 1.5;
        margin-right: 24px;
        position: relative;
        &:not(:last-child)::before {
          content: '';
          position: absolute;
          width: 8px;
          height: 8px;
          display: block;
          background: url('../../assets/right-arrow.svg') no-repeat;
          bottom: 5px;
          right: -16px; }
        &:first-child {
          bottom: 4px;
          &::before {
            bottom: 1px; } } } } }
  @media screen and (max-width: 600px) {
    padding: 0 25px; }
  @media screen and (max-width: 480px) {
    .item-code {
      font-size: 14px; } } }

.categories-dropdown {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto -15px;
  padding: 0 15px 15px;
  background-color: #ececec;
  @media screen and (max-width: 600px) {
    padding: 0; } }

//
.item-content {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px 10px;
  min-height: 750px;
  height: 100%;
  @media screen and (min-width: 1000px) and (max-width: 1200px) {
    min-height: 850px; } }

.img-block {
  margin-top: 10px;
  .img-wrapper {
    height: 650px;
    img {
      transition: all .5s ease-out; }
    &:hover {
      img {
        transform: scale(1.03); } } }
  @media screen and (min-width: 1000px) {
    width: 50%;
    padding: 0 10px;
    float: left; }
  @media screen and (max-width: 1000px) {
    margin-bottom: 40px;
    .img-wrapper {
      height: 500px; } }
  @media screen and (max-width: 650px) {
    margin-bottom: 25px; }
  @media screen and (max-width: 480px) {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
    .img-wrapper {
      height: 400px; } } }

.top-text-block {
  padding: 10px 20px 0 40px;
  h1 {
    font-size: 36px;
    color: $dark-color; }
  .options-picker {
    margin-top: 45px;
    margin-bottom: 60px;
    display: flex;
    p {
      font-size: 16px;
      color: $dark-color; }
    .model-picker {
      margin-left: 60px; } }
  @media screen and (min-width: 1000px) {
    width: 50%;
    float: right; }
  @media screen and (max-width: 1000px) {
    .options-picker {
      margin-bottom: 40px; } }
  @media screen and (max-width: 650px) {
    h1 {
      font-size: 28px; }
    .options-picker {
      margin: 30px 0; } }
  @media screen and (max-width: 480px) {
    padding: 10px 0 0 20px;
    h1 {
      font-size: 24px; }
    .options-picker {
      p {
        font-size: 14px;
        margin-bottom: 5px; }
      .model-picker {
        margin-left: auto; } } }
  @media screen and (max-width: 370px) {
    .options-picker {
      flex-wrap: wrap;
      .model-picker {
        margin-top: 15px;
        margin-left: 0;
        width: 100%; } } } }


.bottom-text-block {
  padding: 0 20px 10px 40px;
  .price {
    font-size: 26px;
    font-weight: 500;
    color: $dark-color; }
  .buttons-row {
    margin-top: 35px;
    margin-bottom: 25px;
    display: flex;
    .buy-success {
      background-color: $main-color;
      width: 300px;
      min-width: 250px;
      border: solid 1px $main-color;
      color: #fff;
      border-radius: 5px;
      height: 46px;
      margin-right: 9px;
      font-size: 18px;
      line-height: 1.5;
      font-weight: 500;
      text-transform: uppercase;
      transition: all .3s ease;
      &:hover {
        background-color: #2DB32D; } }
    .buttons-inner-row {
      display: flex; }
    .buy-secondary {
      background-color: #fff;
      width: 220px;
      border: solid 1px $main-color;
      color: $main-color;
      border-radius: 5px;
      height: 46px;
      margin-right: 18px;
      font-size: 18px;
      line-height: 1.5;
      font-weight: 500;
      text-transform: uppercase;
      transition: all .3s ease;
      &:hover {
        background-color: rgba(51, 204, 51, 0.05); } }
    .favs-add {
      display: block;
      width: 18px;
      min-width: 18px;
      height: 18px;
      background: url('../../assets/like.svg') no-repeat;
      margin-top: 14px;
      margin-left: auto;
      &:hover {
        background: url('../../assets/like-hover.svg') no-repeat; } }
    .favs-added {
      background: url('../../assets/like-hover.svg') no-repeat; } }
  .divider {
    width: 100%;
    height: 1px;
    background-color: #ececec; }
  h3 {
    font-size: 18px;
    font-weight: 500;
    color: $dark-color;
    margin-bottom: 22px;
    margin-top: 16px;
    line-height: 1; }
  @media screen and (min-width: 1000px) {
    width: 50%;
    float: right; }
  @media screen and (min-width: 1000px) and (max-width: 1200px) {
    .buttons-row {
      flex-wrap: wrap;
      .buttons-inner-row {
        width: 100%;
        margin-top: 10px; }
      .favs-add {
        margin-left: 10px; } } }
  @media screen and (max-width: 650px) {
    .price {
      font-size: 24px; }
    .buttons-row {
      flex-wrap: wrap;
      .buy-success {
        width: 100%;
        margin-right: 0; }
      .buttons-inner-row {
        width: 100%;
        margin-top: 15px; }
      .buy-secondary {
        width: 85%; }
      .favs-add {
        margin-right: 5%; } } }
  @media screen and (max-width: 480px) {
    padding: 10px 10px 0 20px;
    .buttons-row {
      margin-top: 15px; } } }


.fast-buy {
  span {
    font-size: 14px;
    line-height: 1.5;
    color: $dark-color;
    margin-bottom: 8px;
    display: block;
    font-weight: 500;
    cursor: default; }
  input {
    width: 185px;
    border: dashed 1px $main-color;
    background-color: #fff;
    color: #777;
    border-radius: 5px;
    display: block;
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 14px;
    padding-left: 18px;
    padding-right: 10px;
    height: 36px;
    transition: all .5s ease;
    &::placeholder {
      color: #777;
      font-size: 14px;
      font-weight: 500; }
    &:focus {
      outline: none;
      border: solid 1px $main-color; } }
  @media screen and (max-width: 480px) {
    input {
      width: 200px; } } }

.buy-now {
  width: 250px;
  margin: 4px 0;
  background-color: $main-color;
  border: solid 1px $main-color;
  color: #fff;
  border-radius: 5px;
  height: 46px;
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
  transition: all .3s ease;
  &:hover {
    background-color: #2DB32D; } }

//options-picker

.custom-color-picker {
  div {
    display: inline-block;
    margin-right: 14px; }

  input[type="radio"] {
    display: none;

    + label {
      color: $dark-color;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;

      span {
        display: inline-block;
        width: 25px;
        height: 25px;
        vertical-align: middle;
        cursor: pointer;
        box-sizing: content-box;
        border: 1px solid #ececec;
        border-radius: 5px;
        background-repeat: no-repeat;
        background-position: center;
        text-align: center;
        line-height: 25px;
        transition: all .3s ease; } }

    &#gold + label span {
      background-color: #F8E5C4; }

    &#spacegray + label span {
      background-color: #5E5D62; }

    &#silver + label span {
      background-color: #C0C0C0; }

    &#white + label span {
      background-color: #fff; }

    &#black + label span {
      background-color: #000; }

    &#blue + label span {
      background-color: #2bc2d9; }

    &#yellow + label span {
      background-color: #f9cf45; }

    &#coral + label span {
      background-color: #ff7461; }

    &#red + label span {
      background-color: #ff0000; }

    &:checked + label span {
      border: 1px solid $main-color; } } }


.custom-model-picker {
  div {
    display: inline-block;
    margin-right: 10px; }

  input[type="radio"] {
    display: none;

    + label {
      color: $dark-color;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;

      span {
        display: inline-block;
        width: 70px;
        height: 28px;
        vertical-align: middle;
        cursor: pointer;
        border: 1px solid #efefef;
        border-radius: 5px;
        box-sizing: content-box;
        background-repeat: no-repeat;
        background-position: center;
        text-align: center;
        line-height: 28px;
        transition: all .3s ease; } }

    &#color-4 + label span {
      background-color: #fff; }

    &#color-5 + label span {
      background-color: #fff; }

    &:checked + label span {
      border: 1px solid $main-color; } } }

.no-display {
  display: none; }
