$main-color: #33cc33;
$dark-color: #404040;

.signin-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  h2 {
    font-size: 24px;
    font-weight: 500;
    color: $dark-color;
    line-height: 1.5;
    font-family: 'Roboto', sans-serif;
    margin-left: 5px; }
  input {
    background-color: #ececec;
    &:focus {
      background-color: #fff; } }
  .auth-btn {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 500;
    text-align: center;
    border: solid 1px #555;
    background-color: #ececec;
    color: $dark-color;
    margin-bottom: 20px;
    cursor: pointer;
    transition: all .5s ease;
    &:hover {
      border: solid 1px $main-color;
      color: darken($main-color, 5%);
      background-color: #fff; } } }
