$main-color: #33cc33;
$dark-color: #404040;

.admin-content {
  padding-top: 20px;
  .admin-home {
    padding: 5px 15px;
    border-radius: 10px;
    border: solid 1px #ccc;
    transition: all .5s ease;
    background-color: #fff;
    a {
      color: $dark-color;
      font-size: 16px; }
    &:hover {
      border: solid 1px $main-color; } }
  .admin-logout {
    @extend .admin-home;
    float: right;
    cursor: pointer; } }

.add-item {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
  margin-top: 10px;
  input {
    color: $dark-color;
    background-color: #fff;
    width: 200px; }
  select {
    border: dashed 1px $main-color;
    border-radius: 5px;
    width: 200px;
    height: 36px;
    color: $dark-color;
    font-size: 14px;
    font-weight: 500;
    padding-left: 18px;
    padding-right: 4px;
    margin-bottom: 16px;
    transition: all .3s ease;
    background-color: #fff;
    &:focus {
      border: solid 1px $main-color;
      outline: none; } }
  .btn-add {
    width: 220px;
    margin-top: 10px; }
  .option-add {
    width: 220px;
    height: 40px;
    border-radius: 5px;
    font-weight: 500;
    text-align: center;
    line-height: 40px;
    border: solid 1px #555;
    color: #555;
    margin-bottom: 20px;
    cursor: default;
    transition: all .3s ease;
    &:hover {
      border: solid 1px $main-color;
      color: $main-color; } } }


.options-list {
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  max-width: 250px;
  flex-wrap: wrap;
  li {
    display: flex;
    margin: 4px 10px 4px 0;
    .box {
      width: 16px;
      height: 16px;
      border: solid 1px #ddd;
      border-radius: 3px; }
    span {
      margin-left: 5px;
      line-height: 16px;
      color: #555; } } }

.display-items {
  margin-top: 70px;
  background-color: #f5f5f5;
  .admin-search {
    margin-bottom: 30px;
    .search-input {
      background-color: #aaa;
      border-radius: 4px; } }
  .admin-item {
    .preview-code {
      align-items: center;
      display: flex;
      font-size: 14px;
      margin-left: 10px; }
    .preview-options {
      margin-left: 20px;
      width: 100px;
      align-items: center;
      li {
        margin-right: 8px; } }
    .preview-info-wrapper {
      display: flex;
      align-items: center;
      position: relative;
      margin-left: 10px;
      .preview-info-hover {
        background-color: #eee;
        padding: 5px;
        border-radius: 5px;
        left: 25px;
        position: absolute;
        display: none;
        span {
          display: block;
          font-size: 13px;
          text-transform: capitalize; } }
      .preview-info {
        background: url('../../assets/info.svg') no-repeat center;
        width: 20px;
        height: 20px; }
      &:hover {
        .preview-info-hover {
          display: block; } } }
    .admin-actions {
      display: flex;
      align-items: center;
      margin-left: 20px;
      .admin-edit {
        background: url('../../assets/edit.svg') no-repeat center;
        width: 20px;
        height: 20px;
        cursor: pointer; }
      .admin-delete {
        background: url('../../assets/delete.svg') no-repeat center;
        width: 20px;
        height: 20px;
        margin-left: 20px;
        cursor: pointer;
        &:hover {
          background: url('../../assets/delete-hover.svg') no-repeat center; } } } } }



.gold {
  background-color: #F8E5C4; }
.spacegray {
  background-color: #5E5D62; }
.silver {
  background-color: #C0C0C0; }
.white {
  background-color: #fff; }
.black {
  background-color: #000; }
.blue {
  background-color: #2bc2d9; }
.yellow {
  background-color: #f9cf45; }
.coral {
  background-color: #ff7461; }
.red {
  background-color: #ff0000; }
