@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

.static-page {
  min-height: 60vh;
  position: relative; }

.static-page-sidebar {
  width: 330px;
  min-width: 330px;
  padding: 0 15px;
  box-sizing: content-box;
  position: absolute;
  top: 0;
  @media screen and (max-width: 600px) {
    max-width: 100%;
    width: 100%;
    position: relative; } }

.static-page-content {
  width: 100%;
  padding: 20px 15px 20px 43px;
  box-sizing: content-box;
  h3, h5, p {
    color: $dark-color; }
  p {
    font-size: 16px; }
  @media screen and (max-width: 780px) {
    padding-left: 35px;
    h3 {
      font-size: 24px; } }
  @media screen and (max-width: 600px) {
    padding-left: 27px;
    h3 {
      font-size: 24px; } } }
