@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap&subset=cyrillic');

$dark-color: #404040;
$main-color: #33cc33;

footer {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  color: $dark-color;
  margin-top: 70px;
  @media screen and (max-width: 750px) {
    margin-top: 30px; } }

.benefits-wrapper {
  background-color: #fff;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  .benefits {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    @media screen and (max-width: 750px) {
      flex-wrap: wrap; }
    .benefit {
      width: 25%;
      display: flex;
      border-right: solid 1px #ddd;
      padding: 15px 30px;
      margin-top: 15px;
      margin-bottom: 15px;
      height: 90px;
      align-items: center;
      &:nth-child(4) {
        border: none; }
      p {
        text-transform: uppercase;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.5;
        margin-left: 30px;
        margin-top: 15px; }
      .img-wrapper {
        height: 60px;
        width: 60px;
        min-width: 60px; }
      @media screen and (max-width: 1100px) {
        padding: 15px 20px;
        p {
          margin-left: 20px; } }
      @media screen and (max-width: 1000px) {
        display: block;
        height: 155px;
        text-align: center;
        p {
          margin-left: 0; }
        .img-wrapper {
          width: 100%; } }
      @media screen and (max-width: 750px) {
        width: 50%;
        &:nth-child(2) {
          border: none; } }
      @media screen and (max-width: 480px) {
        width: 100%;
        margin: 0;
        border: none;
        display: flex;
        height: 90px;
        p {
          margin-left: 20px;
          text-align: left; }
        .img-wrapper {
          width: 60px;
          min-width: 60px; } } } } }

.main-footer-wrapper {
  background-color: #ececec;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  .main-footer {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    padding: 0 30px;
    margin-top: 30px;
    margin-bottom: 50px;
    .call-block {
      width: 25%;
      @media screen and (max-width: 1000px) {
        width: 30%; }
      @media screen and (max-width: 750px) {
        width: 40%; }
      h6 {
        font-size: 16px;
        font-weight: 700;
        line-height: 1.5; }
      .number {
        font-size: 22px;
        font-weight: 700;
        &:hover {
          cursor: default; } }
      span {
        display: block;
        font-size: 12px;
        font-weight: 700; } }
    nav {
      width: 25%;
      ul {
        padding-left: 0;
        li {
          list-style-type: none;
          font-size: 16px;
          font-weight: 500;
          line-height: 30px;
          &:hover {
            a {
              cursor: default; } } } } }
    .join-block {
      min-width: 21%;
      margin-left: auto;
      p {
        font-size: 16px;
        font-weight: 500; }
      ul {
        padding-left: 0;
        li {
          display: inline-block;
          height: 40px;
          width: 40px;
          border: 1px solid #ddd;
          transition: all .3s ease;
          margin-right: 12px;
          line-height: 35px;
          padding-left: 9px;
          cursor: default;
          &:hover {
            background-color: #ddd;
            a {
              cursor: default; } } } } }
    @media screen and (max-width: 700px) {
      flex-wrap: wrap;
      .call-block {
        width: 50%; }
      nav {
        width: 60%;
        order: 2;
        margin-top: 15px; } }
    @media screen and (max-width: 480px) {
      margin-bottom: 30px;
      .call-block {
        width: 100%; }
      nav {
        order: 1;
        width: 100%;
        margin-top: 25px; }
      .join-block {
        width: 100%;
        order: 2;
        margin-top: 15px; } } }

  .secondary-footer {
    max-width: 1440px;
    margin: 0 auto;
    padding: 15px 30px 50px;
    border-top: solid 1px #ccc;
    font-size: 14px;
    display: flex;
    .about-us a {
      cursor: default;
      color: $dark-color; }
    .about-developer {
      margin-left: auto;
      min-width: 21%; }
    @media screen and (max-width: 480px) {
      flex-wrap: wrap;
      padding-bottom: 30px;
      .about-developer {
        width: 100%;
        margin-top: 5px; } } } }
